import {AppBar, Box, Divider, FormControl, InputLabel, List, ListItem, ListItemButton, ListItemIcon, ListItemText, MenuItem, Select, Toolbar, Typography} from "@mui/material";
import {Inbox as InboxIcon, Mail as MailIcon} from "@mui/icons-material";
import {useState} from "react";
import logoImg from "../../assets/images/logo.svg";
import {useSelector} from "react-redux";

export const Sidebar = () => {
  const theme = useSelector(state => state["themeToggle"]["theme"]);
  const [age, setAge] = useState("v1.2");

  const handleChange = (e) => {
    setAge(e.target.value);
  };

  return (
    <Box id="Sidebar">
      <AppBar color="transparent" position="static" elevation={0} sx={{p: "0 0.5rem"}}>
        <Toolbar disableGutters sx={{justifyContent: "space-between"}}>
          <Typography variant="h5" fontWeight="bold">
            <img src={logoImg} alt="logo" style={{height: "1.25em", verticalAlign: "text-bottom"}}/>&nbsp;CSS Examples
          </Typography>
          {/* TODO: version selection */}
          <FormControl sx={{minWidth: "10ch"}} size="small">
            <InputLabel id="demo-select-small" sx={{color: theme.palette.primary}}>Version</InputLabel>
            <Select
              labelId="demo-select-small"
              id="demo-select-small"
              value={age}
              label="Version"
              onChange={handleChange}
              sx={{"& .MuiOutlinedInput-root": {borderColor: theme.palette.primary}}}
            >
              <MenuItem value="v1.2">v1.2</MenuItem>
              <MenuItem value="v1.1">v1.1</MenuItem>
              <MenuItem value="v1.0">v1.0</MenuItem>
            </Select>
          </FormControl>
        </Toolbar>
      </AppBar>
      <Divider/>
      <List>
        {["Inbox", "Starred", "Send email", "Drafts"].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon/> : <MailIcon/>}
              </ListItemIcon>
              <ListItemText primary={text}/>
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Divider/>
      <List>
        {["All mail", "Trash", "Spam"].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon/> : <MailIcon/>}
              </ListItemIcon>
              <ListItemText primary={text}/>
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Divider/>
      <List>
        {["All mail", "Trash", "Spam"].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon/> : <MailIcon/>}
              </ListItemIcon>
              <ListItemText primary={text}/>
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Divider/>
      <List>
        {["All mail", "Trash", "Spam"].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon/> : <MailIcon/>}
              </ListItemIcon>
              <ListItemText primary={text}/>
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Divider/>
      <List>
        {["All mail", "Trash", "Spam"].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon/> : <MailIcon/>}
              </ListItemIcon>
              <ListItemText primary={text}/>
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );
};
