import {useState} from "react";
import {Box, CssBaseline, Drawer, Grid} from "@mui/material";
import {BrowserRouter} from "react-router-dom";
import {Header} from "./containers/Header";
import {Main} from "./containers/Main";
import {Footer} from "./containers/Footer";
import {Sidebar} from "./containers/Sidebar";
import ScrollToTop from "react-scroll-to-top";
import {ArrowUpward} from "@mui/icons-material";
import "../assets/styles/App.css";
import {useSelector} from "react-redux";

export const App = () => {
  const theme = useSelector(state => state["themeToggle"]["theme"]);
  const [mobileOpen, setMobileOpen] = useState(false);

  const toggleSidebar = () => {
    setMobileOpen(!mobileOpen);
  };

  return (
    <Grid id="App" container columns={100}>
      <CssBaseline/>
      <BrowserRouter>
        <Grid item component="nav" xl={17}>
          <Drawer
            variant="temporary"
            open={mobileOpen}
            onClose={toggleSidebar}
            ModalProps={{keepMounted: true}}
            sx={{
              display: {xs: "block", sm: "none"},
              "& .MuiDrawer-paper": {boxSizing: "border-box", width: "50vw"},
            }}
          >
            <Sidebar/>
          </Drawer>
          <Drawer
            variant="permanent"
            open
            sx={{
              "& .MuiDrawer-paper": {display: {xs: "none", sm: "block"}, width: "17vw"}
            }}
          >
            <Sidebar/>
          </Drawer>
        </Grid>
        <Grid item xl={83}>
          <Header toggleSidebar={toggleSidebar}/>
          <Main/>
          <Footer/>
          <ScrollToTop
            smooth
            component={<ArrowUpward sx={{fontSize: "2rem", color: "white"}}/>}
            style={{backgroundColor: theme.palette.primary, width: "3rem", height: "3rem", right:"1rem"}}
          />
        </Grid>
      </BrowserRouter>
    </Grid>
  );
};
