import {AppBar, IconButton, Stack, Toolbar, Typography} from "@mui/material";
import {GitHub, Menu, Settings, Translate} from "@mui/icons-material";
import "../../assets/styles/Header.css";
import {useEffect, useState} from "react";
import {useSelector} from "react-redux";

export const Header = (props) => {
  const {toggleSidebar} = props;
  const theme = useSelector(state => state["themeToggle"]["theme"]);
  const [elevation, setElevation] = useState(0);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      const scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
      scrollTop > 0 ? setElevation(3) : setElevation(0);
    });
    return () => {window.removeEventListener("scroll", () => console.log("Removed."));};
  }, []);

  return (
    <AppBar
      id="Header"
      position="sticky"
      elevation={elevation}
      sx={{
        color: theme.palette.text.primary,
        backgroundColor: "rgba(255, 255, 255, 0.5)",
        backdropFilter: "blur(2px)",
      }}
    >
      <Toolbar>
        <IconButton color="inherit" edge="start" onClick={toggleSidebar} sx={{mr: 2, display: {sm: "none"}}}>
          <Menu/>
        </IconButton>
        <Typography variant="h5" noWrap sx={{display: {"sm": "none"}}}>CSS Examples</Typography>
        <Stack flexGrow={1} direction="row" justifyContent="flex-end">
          <IconButton></IconButton>
          <IconButton><Translate sx={{color:theme.palette.primary}}/></IconButton>
          <IconButton><GitHub sx={{color:theme.palette.primary}}/></IconButton>
          <IconButton><Settings sx={{color:theme.palette.primary}}/></IconButton>
        </Stack>
      </Toolbar>
    </AppBar>
  );
};
